.wrapper-experience {
          display: flex;
          background-color: azure;
          flex-direction: column;
          gap: 10px;
          padding: 15px;
          max-width: 350px;
          min-height: 400px;
          -webkit-box-shadow: #FFF 0 -1px 4px, #ff0 0 -2px 10px, #ff8000 0 -10px 20px, red 0 -18px 40px, 1px 3px 13px 11px rgba(209,209,209,0); 
box-shadow: #FFF 0 -1px 4px, #ff0 0 -2px 10px, #ff8000 0 -10px 20px, red 0 -18px 40px, 1px 3px 13px 11px rgba(209,209,209,0);
}

.companyh1 {     
   color: black;
   font-weight: 700;
   letter-spacing: 0.2rem;
   
}

.wrapper-experience h2 {
       font-weight: 600;
       text-align: center;   
}

.wrapper-experience p {
          font-size: large;
          font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
          text-align: center;   
   }