@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
 
}

.experienceContainer {
  padding: 20px;
}


.wrapper {
          
          padding: 40px;
          width: 100%;
          height: 300px;
          display: flex;
          grid-gap: 20px;
          gap: 20px;
          flex-direction: row;
          align-items: center;
          background-color: khaki
}

.about-yazi {
          min-width: 500px;
          font-size: large;
          font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
          
}
h1 {
          margin-bottom: 2rem;
          color: cornflowerblue;
          text-align: center;
}
img {
   object-fit: contain;
   width: 20%;   
   border-radius: 5px;    
}
.wrapper-experience {
          display: flex;
          background-color: azure;
          flex-direction: column;
          grid-gap: 10px;
          gap: 10px;
          padding: 15px;
          max-width: 350px;
          min-height: 400px; 
box-shadow: #FFF 0 -1px 4px, #ff0 0 -2px 10px, #ff8000 0 -10px 20px, red 0 -18px 40px, 1px 3px 13px 11px rgba(209,209,209,0);
}

.companyh1 {     
   color: black;
   font-weight: 700;
   letter-spacing: 0.2rem;
   
}

.wrapper-experience h2 {
       font-weight: 600;
       text-align: center;   
}

.wrapper-experience p {
          font-size: large;
          font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
          text-align: center;   
   }
.experienceContainer1 {
          display: flex;
          flex-direction: column;
          background-color: darkolivegreen;
          opacity: 0.9;
          padding: 30px;
}

.experienceContainer2 {
          display: flex;
          flex-direction: row;
          grid-gap: 20px;
          gap: 20px;
          justify-content: space-around;
        
}

.education-container {
          display: flex;
          flex-direction: column;
          background-color: blanchedalmond;
}

.social-container {
          display: flex;
          flex-direction: row;
          text-align: center;
          justify-content: center;
          grid-gap: 50px;
          gap: 50px;
          background-color: darkgray;
}


